.footer-container{
    background-color: #575755;
    padding: 100px 0 100px 0;
    .container{
        @media screen and (max-width: 576px) {
            .copyright{
                // padding: 0 0 0 0;
                padding: 10px 0 10px 0;
                font-size: 15px;
                font-weight: bold;
                color: #ffffff;
            }
            .termOfUse{
                color: #ffffff;
                font-size: 15px;
                font-weight: bold;
            }
        }
        .fa-twitter{
            font-size: 30px;
            color: #ffffff;
            cursor: pointer;
        }
        .fa-github{
            font-size: 30px;
            color: #ffffff;
            cursor: pointer;
        }
        .fa-linkedin-in{
            font-size: 30px;
            color: #ffffff;
            cursor: pointer;
        }
    }
    .copyright{
        // padding: 0 0 0 0;
        padding: 10px 0 10px 0;
        font-size: 20px;
        font-weight: bold;
        color: #ffffff;
    }
    .termOfUse{
        color: #ffffff;
        font-size: 20px;
        padding: 10px 0 10px 0;
        font-weight: bold;
    }
}