@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.about-organisms {
  padding: 50px 50px; }

.container-fluid {
  padding-left: 0;
  padding-right: 0; }
  .container-fluid .contact-organisms {
    padding: 100px 0 100px 0; }
    .container-fluid .contact-organisms .heading-section {
      padding: 20px 0 20px 0; }

.detail-contact-molecules {
  padding-right: 15px;
  padding-left: 15px; }


.profile-organisms {
  padding: 50px 0 50px 0; }
  .profile-organisms .container .profile-section {
    text-align: center; }
  @media screen and (max-width: 576px) {
    .profile-organisms .container .profile-heading-section h1 {
      font-size: 30px; }
    .profile-organisms .container .tag-line {
      font-size: 15px; } }
  .profile-organisms .container .profile-heading-section {
    text-align: center; }
  .profile-organisms .container .tag-line {
    text-align: center;
    color: #555555;
    font-weight: bold; }

.portofolio-organisms {
  padding: 50px 0 100px 0; }
  .portofolio-organisms .container .heading-section {
    text-align: center;
    text-transform: uppercase;
    padding: 20px 0 20px 0; }
  .portofolio-organisms .portofolio-item {
    text-align: center; }
    .portofolio-organisms .portofolio-item .row {
      margin-left: 0;
      margin-right: 0; }

.card-portofolio-molecules {
  margin-left: 20px;
  margin-bottom: 20px;
  text-align: center; }
  .card-portofolio-molecules .card {
    height: 20rem;
    text-align: center;
    box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.75);
    border-radius: 4px; }
    .card-portofolio-molecules .card .card-body {
      padding: 10px 0 10px 0; }

@media screen and (max-width: 576px) {
  .navbar .container .nav {
    font-size: 12px; }
  .navbar .container .navbar-brand {
    font-size: 12px;
    font-weight: bold; } }

.navbar .container .navbar-brand {
  cursor: pointer; }

.navbar .container .nav-link {
  cursor: pointer; }

.footer-container {
  background-color: #575755;
  padding: 100px 0 100px 0; }
  @media screen and (max-width: 576px) {
    .footer-container .container .copyright {
      padding: 10px 0 10px 0;
      font-size: 15px;
      font-weight: bold;
      color: #ffffff; }
    .footer-container .container .termOfUse {
      color: #ffffff;
      font-size: 15px;
      font-weight: bold; } }
  .footer-container .container .fa-twitter {
    font-size: 30px;
    color: #ffffff;
    cursor: pointer; }
  .footer-container .container .fa-github {
    font-size: 30px;
    color: #ffffff;
    cursor: pointer; }
  .footer-container .container .fa-linkedin-in {
    font-size: 30px;
    color: #ffffff;
    cursor: pointer; }
  .footer-container .copyright {
    padding: 10px 0 10px 0;
    font-size: 20px;
    font-weight: bold;
    color: #ffffff; }
  .footer-container .termOfUse {
    color: #ffffff;
    font-size: 20px;
    padding: 10px 0 10px 0;
    font-weight: bold; }

body{
  font-family: 'Montserrat', sans-serif;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #ce6666; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #80b1d1; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
}

